import React from "react";
import { BannerHeadCon, BannerHead, JoinBtn } from "../Styles/Header.styles";

const Header = () => {
  return (
    <>
      <BannerHeadCon>
        <BannerHead>
          Register for FREE to meet your favourite creators in their Metaverse
        </BannerHead>
        <JoinBtn
          onClick={() => {
            alert("Clicked");
          }}
        >
          Join Waiting List
        </JoinBtn>
      </BannerHeadCon>
    </>
  );
};

export default Header;
