import styled from "styled-components";
import { COLORS, FONTS, DEVICE } from "../Constants/theme";

export const BannerHeadCon = styled.div`
  width: 70%;
  position: absolute;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 0);
  @media ${DEVICE.mobileL} {
    top: 30%;
  }
`;
export const BannerHead = styled.h1`
  color: ${COLORS.TextColor};
  text-align: center;
  font-size: 3rem;
  font-family: ${FONTS.Carzy};
  @media ${DEVICE.tablet} {
    font-size: 2rem;
  }
  @media ${DEVICE.mobileL} {
    font-size: 1.5rem;
  }
`;
export const JoinBtn = styled.button`
  position: absolute;
  font-size: 20px;
  top: 90%;
  left: 50%;
  transform: translate(-50%, 90%);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  color: white;
  background-color: initial;
  background-image: linear-gradient(#541a0f 0, #0c0d0d 100%);
  border: 5px solid rgb(255 95 5);
  border-radius: 25px;
  height: 50px;
  width: 300px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  font-family: ${FONTS.Monster}; @media ${DEVICE.mobileL} {
    top: 100%;
  }
  &:hover {
    background-color: initial;
    background-image: linear-gradient(rgb(255 95 5) 0, rgb(255 95 5) 100%);
    border: none;
  }
`;
